<template>
<PageLoader
    v-if="loading"
  />
  
  <div ref="ecpayForm" class="ecpayForm"></div>
   <div class="relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%">
     <OrderPreview
     @dataLoaded="dataLoaded" 
      ref="orderPreview"/>
  </div>
  <div v-if="isDataLoaded" class="relative mx-auto sm:max-w-xl md:max-w-full   mb-8" style="max-width:80%">
    <button v-if="showAckButton" @click.prevent="ackUser"  class="mt-2 focus:outline-none text-white bg-yellow-700 hover:bg-yellow-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">{{ isOrderAcknowlodged ?'Put on hold' : 'Acknowledge Order'}}</button>
    <button v-if="!logisticsOrderCreated" @click.prevent="createLogisticsOrder"  class="mt-2 focus:outline-none text-white bg-yellow-700 hover:bg-yellow-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">Create Logistics Order</button>
    <div v-else class="flex p-4 mb-4 text-sm text-blue-800 border border-blue-300 rounded-lg bg-blue-50 dark:bg-gray-800 dark:text-blue-400 dark:border-blue-800" role="alert">
      <svg aria-hidden="true" class="flex-shrink-0 inline w-5 h-5 mr-3" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clip-rule="evenodd"></path>
      </svg>
      <span class="sr-only">Info</span>
      <div>
        <span class="font-medium">Logistics order is in progress</span>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */

import PageLoader from '@/components/PageLoader.vue'
import OrderPreview from '@/components/OrderPreview.vue'
import env from '@/env'
const baseURL = env.baseURL
export default {
  mounted() {
    let orderId = this.$route.params.id
    if(orderId){
      this.orderId = orderId
      // this.$refs.orderInput.value = orderId
      this.$refs.orderPreview.getOrderDetails(orderId)
    }
  },
  components:{
    PageLoader,
    OrderPreview
  },
  data() {
    return {
      orderId:null,
      isDataLoaded:false,
      loading:false,
      showAckButton:false,
      isOrderAcknowlodged:false,
      logisticsOrderCreated:false
    }
  },
  methods: {
    dataLoaded(data) {
      console.log(data,"data is hereee")
      this.isDataLoaded = true
      if(data.isPaid && data.Logistics.id){
        this.showAckButton = true
      }
      this.isOrderAcknowlodged = data.orderAcknowledged
      this.logisticsOrderCreated = data.logisticsOrderCreated
    },
    async createLogisticsOrder() {
      try{
        this.loading = true;
        let {data} = await this.axios.post('admin/create-logistics-order',{
          orderId:this.orderId
        })
        this.loading = false;
        this.logisticsOrderCreated = true
      }catch(e) {
        this.loading = false;
        console.log(e)
        let { data : { message } } = e.response
        this.error = message
        this.$notify({ title: "Error", text: message });
      }
    },
  async downloadPDF() {
    try{
      this.loading = true;
      let {data} = await this.axios.post('orders/pdf-preview',{
        orderId:this.orderId
      })
      this.loading = false;
      let url = `${baseURL()}api/orders/download-pdf/${this.orderId}`
      var newWin = window.open(url); 
      if(!newWin || newWin.closed || typeof newWin.closed=='undefined'){ 
        window.location.href = url
      }
    }catch(e) {
      this.loading = false;
      console.log(e)
      let { data : { message } } = e.response
      this.error = message
      this.$notify({ title: "Error", text: message });
    }
  },
    async getOrderDetails() {
      let orderId = this.$refs.orderInput.value
      if(orderId.length < 5 ){
        this.$notify({ title: "Error", text: 'Please enter a valid order-id' });
        return
      }
      this.orderId = orderId
      this.$refs.orderPreview.getOrderDetails(this.orderId)
    },
    async ackUser() {
      try{
        this.loading = true;
        let {data} = await this.axios.post('admin/acknowledge-order',{
          orderId:this.orderId
        })
        this.loading = false;
        this.isOrderAcknowlodged = !this.isOrderAcknowlodged
        this.$notify({ title: "Success", text: data.message });

    }catch(e) {
        this.loading = false;
        console.log(e)
        let { data : { message } } = e.response
        this.error = message
        this.$notify({ title: "Error", text: message });
      }
    }
  }
}
</script>
